import Vue from 'vue'
import App from './App.vue'
import router from './router'
//引入lib-flexible插件
import 'lib-flexible'
//引入初始化样式表
import "./assets/css/reset.css"
//引入字体图标样式表
import './assets/iconfont/iconfont.css'
//引入过滤器
import './assets/javascript/filter.js'
//引入vuex
import store from './store/index.js'
//引入vant-ui组件库
import Vant from 'vant';
import 'vant/lib/index.css';
Vue.use(Vant);
//引入微信相关的api
import catchinfo from './assets/javascript/catchInfo.js'
Vue.use(catchinfo);
//将全局变量绑定到vue实例根组件上
let Global = {}
Vue.$global = Vue.prototype.$global = Global;
var ua = navigator.userAgent.toLowerCase(); //判断是否是微信浏览器
Vue.$global.isWX = ua.match(/MicroMessenger/i) == "micromessenger";
Vue.$global.isPhone = /(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent);
Vue.$global.isAndroid = /(Android)/i.test(navigator.userAgent);
// 忽略打开微信小程序的组件
Vue.config.ignoredElements = ['wx-open-launch-weapp']

Vue.config.productionTip = false

localStorage.clear()
new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')

