//获取详情页数据
export const getDetailData = (params) => {
    return request({
        method: 'get',
        params,
        url: '/activity/detail/' + params.ac_id,
        token: false
    })
}

//获取详情页登录弹窗的数据
export const getInviteLoginData = (data) => {
    return request({
        method: 'post',
        data,
        url: "/activity/share/accept",
        token: false
    })
}
