import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex)
export default new Vuex.Store({
    state: {
        WXUserInfo: {},
        appUserInfo: {},
        distributionInfo: {},
        WXConfig: {}
    },
    mutations: {
        setWXUserInfo(state, v) {
            state.WXUserInfo = v
        },
        setAppUserInfo(state, v) {
            state.appUserInfo = v
        },
        setDistributionInfo(state, v) {
            state.distributionInfo = v
        },
        setId(state, v) {
            state.WXUserInfo.id = v
        },
        setWXConfig(state, v) {
            state.WXConfig = v
        }
    },
    actions: {},
    getters: {},
    plugins: [createPersistedState({ storage: window.sessionStorage })],
})