import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './routes'
import store from '../store/index'
import {
  getData
} from '../assets/javascript/apis.js'
Vue.use(VueRouter)

//解决使用this.$router.push,跳转向同一个页面会发生报错的问题
//同时能够解决使用this.$router.push,跳转时发生重定向报错的问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

//声明vue-router的实例化对象
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    };
  }
})

//验证token的函数
let testToken = () => {
  if (store.state.appUserInfo.token) {
    let setTime = new Date(store.state.appUserInfo.expired_at)
    setTime = setTime.getTime()
    let nowTime = new Date().getTime()
    if (nowTime > setTime) {
      return false
    } else {
      return true
    }
  } else {
    return false
  }
}

//从地址栏获取参数的函数
let getUrlParam = (name) => {
  let urlParam = decodeURI(window.location.search).substr(1).split("&");
  for (let i = 0; i < urlParam.length; i++) {
    if (urlParam[i].split("=")[0] == name) {
      return urlParam[i].split("=")[1];
    }
  }
  return false;
}

//路由全局前置守卫
