import store from '../../store/index';
import {
    getWXConfig
} from "./apis.js";

export default {
    install(Vue) {
        // 获取微信分享参数
        Vue.prototype.$shareConfig = (
            sharetitle,
            sharedesc,
            shareimg,
            shareurl
        ) => {
            //设置参数默认值
            if (!sharetitle) {
                sharetitle = "分销";
            }
            if (!sharedesc) {
                sharedesc = "分销";
            }
            if (!shareimg) {
                shareimg =
                    "https://jumijupai.oss-cn-beijing.aliyuncs.com/jupai/1534988711050.png";
            }
            if (!shareurl) {
                shareurl = location.origin + '/detail?ac_id=' + store.state.distributionInfo.ac_id + '&from_user_id=' + (store.state.WXUserInfo.id ? store.state.WXUserInfo.id : store.state.distributionInfo.from_user_id);
            }
            const data = {
                jsapis: "updateTimelineShareData,updateAppMessageShareData,onMenuShareTimeline,onMenuShareAppMessage,onMenuShareQQ,onMenuShareWeibo,onMenuShareQZone",
                url: window.location.href.replace(window.location.hash, ""), //当前页面的url，不包含#之后的部分
            };
            console.log(data);
            getWXConfig(data).then((res) => {
                // debugger;
                console.log(res);
                //将微信config验证的信息，更新到vuex中
                store.commit("setWXConfig", res);
                wx.config({
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    appId: store.state.WXConfig.appId, // 必填，公众号的唯一标识
                    timestamp: store.state.WXConfig.timestamp, // 必填，生成签名的时间戳
                    nonceStr: store.state.WXConfig.nonceStr, // 必填，生成签名的随机串
                    signature: store.state.WXConfig.signature, // 必填，签名
                    jsApiList: [
                        'updateTimelineShareData',
                        'updateAppMessageShareData',
                        "onMenuShareTimeline",
                        "onMenuShareAppMessage",
                        "onMenuShareQQ",
                        "onMenuShareWeibo",
                        'onMenuShareQZone'
                    ], // 必填，需要使用的JS接口列表
                    openTagList: ['wx-open-launch-weapp']
                })
            });

            wx.ready(function () {
                wx.onMenuShareTimeline({
                    title: sharetitle,
                    desc: sharedesc,
                    link: shareurl,
                    imgUrl: shareimg,
                    success: function () {},
                    cancel: function () {}
                });
                wx.onMenuShareQQ({
                    title: sharetitle,
                    desc: sharedesc,
                    link: shareurl,
                    imgUrl: shareimg,
                    success: function () {},
                    cancel: function () {}
                });
                wx.onMenuShareAppMessage({
                    title: sharetitle,
                    desc: sharedesc,
                    link: shareurl,
                    imgUrl: shareimg,
                    success: function () {},
                    cancel: function () {}
                });
                wx.onMenuShareWeibo({
                    title: sharetitle,
                    desc: sharedesc,
                    link: shareurl,
                    imgUrl: shareimg,
                    success: function () {},
                    cancel: function () {}
                });
                wx.onMenuShareQZone({
                    title: sharetitle, // 分享标题
                    desc: sharedesc, // 分享描述
                    link: '', // 分享链接
                    imgUrl: '', // 分享图标
                    success: function () {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });
                wx.updateAppMessageShareData({
                    title: sharetitle, // 分享标题
                    desc: sharedesc, // 分享描述
                    link: shareurl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: shareimg, // 分享图标
                    success: function () {
                        // 设置成功
                    }
                });
                wx.updateTimelineShareData({
                    title: sharetitle, // 分享标题
                    link: shareurl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                    imgUrl: shareimg, // 分享图标
                    success: function () {
                        // 设置成功
                    }
                });
            });
        };
        //调起微信支付接口
        Vue.prototype.$payConfig = (
            timestamp = 0, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
            nonceStr = '', // 支付签名随机串，不长于 32 位
            apackage = '', // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
            signType = '', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
            paySign = '', // 支付签名
            success,
            fail
        ) => {
            const data = {
                jsapis: "chooseWXPay",
                url: window.location.href.replace(window.location.hash, ""), //当前页面的url，不包含#之后的部分
            };
            getWXConfig(data).then((res) => {
                console.log(res);
                //将微信config验证的信息，更新到vuex中
                store.commit("setWXConfig", res);
                wx.config({
                    debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                    appId: store.state.WXConfig.appId, // 必填，公众号的唯一标识
                    timestamp: store.state.WXConfig.timestamp, // 必填，生成签名的时间戳
                    nonceStr: store.state.WXConfig.nonceStr, // 必填，生成签名的随机串
                    signature: store.state.WXConfig.signature, // 必填，签名
                    jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表
                })
            })

            wx.ready(function () {
                wx.chooseWXPay({
                    timestamp: timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                    nonceStr: nonceStr, // 支付签名随机串，不长于 32 位
                    package: apackage, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                    signType: signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                    paySign: paySign, // 支付签名
                    success: success,
                    fail: fail
                });
            })

        }
    }
};